 * @LastEditTime: 2019-07-29 10:18:50
 -->
<template>
  <div class="page flex-v">
    <div class="head flex-v-center">
      <el-breadcrumb separator-class="el-icon-arrow-right"
        class="my-breadcrumb">
        <el-breadcrumb-item :to="{ name: 'CheckIn' }">登记台</el-breadcrumb-item>
        <el-breadcrumb-item>人员信息</el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <div class="query-form">
      <el-form inline
        size='small'
        class="default-form-style">
        <el-form-item label='姓名：'>
          <el-input placeholder="请输入"
            v-model="queryObj.userName"
            clearable></el-input>
        </el-form-item>
        <el-form-item label='身份证号：'>
          <el-input type="number"
            placeholder="请输入"
            v-model="queryObj.idNo"
            clearable></el-input>
        </el-form-item>
        <el-form-item label='户籍地址：'>
          <el-input placeholder="请输入"
            v-model="queryObj.residenceAddress"
            clearable></el-input>
        </el-form-item>
        <el-form-item>
          <el-button icon="el-icon-search"
            round
            @click="onSubmit">查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="table-box flex-item">
      <el-table :data='tableData'
        height='100%'
        header-cell-class-name='custom-table-cell'
        v-loading="loading"
        stripe>
        <el-table-column label='序号'
          type='index'>
          <template v-slot='scope'>
            {{scope.$index + (pageNum - 1) * pageSize + 1}}
          </template>
        </el-table-column>

        <el-table-column label='姓名'
          prop='userName'
          show-overflow-tooltip></el-table-column>
        <el-table-column label='性别'
          width="100">
          <template v-slot="scope">{{scope.row.sexTypeStr | emptyData}}</template>
        </el-table-column>
        <el-table-column label='年龄'
          width="100">
          <template v-slot="scope">{{scope.row.age | emptyData}}</template>
        </el-table-column>
        <el-table-column label='民族'
          show-overflow-tooltip>
          <template v-slot="scope">{{scope.row.nationCase | emptyData}}</template>
        </el-table-column>
        <el-table-column label='身份证号'
          show-overflow-tooltip>
          <template v-slot="scope">{{scope.row.idNo | emptyData}}</template>
        </el-table-column>
        <el-table-column label='联系电话'
          width="160">
          <template v-slot="scope">{{scope.row.contactPhone | emptyData}}</template>
        </el-table-column>
        <el-table-column label='户籍地址'
          show-overflow-tooltip>
          <template v-slot="scope">{{scope.row.residenceAddress | emptyData}}</template>
        </el-table-column>
        <el-table-column label='操作'
          width="160">
          <template slot-scope="scope">
            <el-button class="btn-hollowing"
              style="padding: 6px 22px"
              @click="goCheckInPerson(scope.row)">选择</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="foot">
      <bj-pagination :query-obj="queryObj"
        :query-url="queryUrl"
        @listResult="getListResult"
        ref="paginationEvent"
        @controlLoading="controlLoading" />
    </div>
  </div>
</template>

<script>
export default {
  name: 'PersonList',
  data() {
    return {
      loading: false,
      queryObj: {
        userName: '',
        idNo: '',
        residenceAddress: ''
      },
      queryUrl: 'user/getPersionDoc/getByPage',
      pageNum: 1,
      pageSize: 10,
      loading: false,
      tableData: []
    }
  },

  methods: {
    // 进入登记人员
    goCheckInPerson({ idNo, sysGuid }) {
      let routers = {
        name: 'CheckInPerson',
        query: { sysGuid, tjsysGuid: 'add' }
      }
      this.$post('/user/examUser/getExamTimes', {
        idNo,
        type: 'year'
      })
        .then(({ data }) => {
          let [complete, noComplete] = data.split(',')
          if (1 * noComplete > 0) return this.$message.warning('正在体检中...')
          if (1 * complete > 0) {
            this.$confirm(`本年度已体检过${complete}次`, {
              title: '温馨提示',
              showClose: false,
              customClass: 'msg-box'
            })
              .then(() => {
                this.$router.push(routers)
              })
              .catch(e => e)
          } else {
            this.$router.push(routers)
          }
        })
        .catch(err => this.$message.error(err))
    },
    // 查询
    onSubmit() {
      this.$refs.paginationEvent.initPageList(1)
    },

    //获取数据
    getListResult({ list, pageNum, pageSize }) {
      this.tableData = list
      this.pageNum = pageNum
      this.pageSize = pageSize
    },
    controlLoading(loading) {
      this.loading = loading
    }
  }
}
</script>

<style lang="less" scoped>
.head {
  padding: 0 70px 0 20px;
  height: 70px;
  border-bottom: 1px solid #eaeff5;
}

.imitate-btn {
  display: inline-block;
  padding: 0 22px;
  line-height: 26px;
  border-radius: 4px;
  border: 1px solid currentColor;
  background-color: #f2fdfb;
  color: #07caa6;
}

.plain-btn.el-button.is-plain {
  border-color: currentColor;
  background-color: #f2fdfb;
  color: #07caa6;
  &:hover {
    color: #07caa6;
  }
}
</style>


